body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.intro-head {
    margin-top:80px;
    margin-left: 50px;
    -webkit-animation: transitionIn 1s;
            animation: transitionIn 1s;
}
#intro {
  background-image: linear-gradient(to top, rgba(201, 219, 255, 0), rgba(201, 219, 255, 1));
  width: 100%;
}

.picofme {
  margin-top: 40px;
  margin-bottom: 30px;
  margin-left: 30px;
  -webkit-animation: transitionIn 1s;
          animation: transitionIn 1s;
}
#nav-header {
    background-image: linear-gradient(to top, rgba(201, 219, 255, 1), rgb(182, 197, 229));
    color: black;
    text-decoration: none;
    font-weight: bold;
    padding-right: 10%;
}

.nav-item {
    padding: 0 5% 0 5%;
}
.footer {
    text-align: center;
    position: fixed;
    bottom: 0;
}

#social_icon {
    width: auto;
    height: auto;
    cursor: pointer;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

#social_icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.projectCard {
    word-wrap: break-word;
    min-width: 18rem;
    max-width: 24rem;
}

.projectCard:hover {
    background-color: #ebf2fc;
}

.projectFilter {
    min-width: 100%;
}

.projectFilterDropdown {
    min-width: 100%;
}
.employment-time {
    font-size: small;
}

@-webkit-keyframes transitionIn {
  from {
      opacity: 0;
      -webkit-transform: rotateX(-10deg);
              transform: rotateX(-10deg);
  }
  to {
      opacity: 1;
      -webkit-transform: rotateX(0);
              transform: rotateX(0);
  }
}
@keyframes transitionIn {
  from {
      opacity: 0;
      -webkit-transform: rotateX(-10deg);
              transform: rotateX(-10deg);
  }
  to {
      opacity: 1;
      -webkit-transform: rotateX(0);
              transform: rotateX(0);
  }
}

